import logo from '../../imgs/logo.jpg';
import './Header.scss';
import Dropdown from 'react-bootstrap/Dropdown';
import { useTranslation } from 'react-i18next';
import cn from 'classnames';

function Header(props) {
  let { t, i18n } = useTranslation();
  let { active=0 } = props;

  function changeLanuage(key, e) {
    i18n.changeLanguage(key);
    sessionStorage.setItem('language', key);
  }

  return (
    <div className="header flex-m">
      <a href="/">
        <img className='logo' src={logo} alt="" />
      </a>

      <div className='l-menu flex flex-1'>
        <div onClick={() => { window.location = './index.html' }} className={cn('lm-item', active == 0 ? 'active' : '')}>{t("head.m1")}</div>
        <div onClick={() => { window.location = './fund.html' }} className={cn('lm-item', active == 1 ? 'active' : '')}>{t("head.m2")}</div>
        <div onClick={() => { window.location = './news.html' }} className={cn('lm-item', active == 2 ? 'active' : '')}>{t("head.m3")}</div>
      </div>
      <div className='r-menu'>
        <Dropdown onSelect={changeLanuage}>
          <Dropdown.Toggle variant="success" id="dropdown-basic">
            {i18n.language == 'en' ? 'Languages' : '選擇語言'}
          </Dropdown.Toggle>

          <Dropdown.Menu>
            <Dropdown.Item eventKey='en'>English</Dropdown.Item>
            <Dropdown.Item eventKey='zh'>繁體中文</Dropdown.Item>
          </Dropdown.Menu>
        </Dropdown>
      </div>
    </div>
  );
}

export default Header;
