import './App.scss';
import Header from '../../components/Header/Header';
import Footer from '../../components/Footer/Footer';
import resonImg from '../../imgs/img_01.png';
import sImg_1 from '../../imgs/f-s1.jpg';
import sImg_2 from '../../imgs/f-s2.jpg';
import sImg_3 from '../../imgs/f-s3.jpg';
import sImg_4 from '../../imgs/f-s4.png';
import { WOW } from 'wowjs';
import React, { useEffect } from 'react';

function App() {
  useEffect(() => {
    var wow = new WOW({
      boxClass: "wow",    //需要执行动画的元素的 class
      animateClass: "animated", // animation.css 动画的 class
      offset: 0, // 距离可视区域多少开始执行动画
      mobile: true, // 是否在移动设备上执行动画
      live: true // 异步加载的内容是否有效
      })
      wow.init();
  }, []);
  return (
    <div className="fund">
      <Header active='1'></Header>
      <div className='content'>
        <div className='top'>
          <div className='title tc'>在HKGF参与投資的各種方式</div>
          <div className='box flex'>
            <div className='n-item flex-1 bounceInLeft wow'>
              <img className='ni-img' src={resonImg} alt="" />
              <div className='ni-title tc'>自訂投資組合</div>
              <div className='ni-subtit'>廣泛的投資產品讓您更靈活的建立專屬的投資組合</div>
            </div>
            <div className='n-item flex-1'>
              <img className='ni-img' src={resonImg} alt="" />
              <div className='ni-title tc'>專業投資顧問</div>
              <div className='ni-subtit'>無論您是投資新手，或只需要為目前的投資組合尋求意見，我們專業的客戶投資專員將根據您的風險承擔能力，竭力祝您建立合適的投資組合。</div>
            </div>
            <div className='n-item flex-1 wow bounceInRight'>
              <img className='ni-img' src={resonImg} alt="" />
              <div className='ni-title tc'>FSM管理組合</div>
              <div className='ni-subtit'>這絕對是最無憂無慮的投資方式，該項服務旨在透過授權投資專家為您做出交易買賣決定，讓您直接坐享投資成果。</div>
            </div>
          </div>
        </div>
          
        <div className='service'>
          <div className='title tc'>投資產品及服務</div>
          <div className='sub-title tc'>廣泛的投資產品讓您更靈活地建構專屬的投資組合</div>
          <div className='s-row flex'>
            <div className='s-col flex-1'>
              <img src={sImg_1} alt="" />
              <div>数字化金融资讯服务</div>
            </div>
            <div className='s-col flex-1'>
              <img src={sImg_2} alt="" />
              <div>数字化资产管理</div>
            </div>
            <div className='s-col flex-1'>
              <img src={sImg_3} alt="" />
              <div>数字化技术架构服务</div>
            </div>
            <div className='s-col flex-1'>
              <img src={sImg_4} alt="" />
              <div>金融衍生服务板块</div>
            </div>
          </div>
          <div className='desc flex'>
            <div className='flex-1 d-item'>
              <div className='title'>01 数字化金融咨询服务</div>
              <div className='sub-title'>擴充多元化投資渠道，增加對不同資產類別的接觸路徑和更多的流動性選擇，基於區塊鏈去中心化、可追溯、透明公平的特性為資本服務。</div>
              <div className='point'>數字化戰略咨詢服務​</div>
              <div className='point'>數字化改造及架構搭建</div>
              <div className='point'>數字資產上鏈及發行服務</div>
              <div className='point'>數字資產募資與流動性服務</div>
              <div className='point'>其他咨詢服務等</div>
            </div>
            <div className='flex-1'>
              <div className='title'>02 數字化資產管理​</div>
              <div className='sub-title'>篩選符合資質的項目和產品進行一站式孵化/數字化佈局，並為其提供流動性、資金、技術、國際社群、運營支持等個性化解決方案和服務</div>
              <div className='point'>提供多元化新型投資資產類別</div>
              <div className='point'>提供政策和程序構成法律標準的服務​</div>
              <div className='point'>提升資產的流動性和透明度​，降低交易成本，提高交易效率及可用性​</div>
              <div className='point'>匯聚全球優質資本、機構及服務商，為項目和客戶提供更多發展機遇</div>
            </div>
          </div>
          <div className='desc flex'>
          <div className='flex-1'>
              <div className='title'>​​03 數字化技術架構服務​​</div>
            </div>
            <div className='flex-1'>
              <div className='title'>04 金融衍生服務板塊</div>
              <div className='sub-title'>​深耕數字化行業建設，衍生教育、電商等更多版塊服務，助推行業發展​​新闻快讯</div>
            </div>
          </div>
        </div>

        <div className='taurus'>
          <div className='title'>Taurus基金</div>
          <div className='sub-title'>Taurus为HKGF旗下基于区块链加密演算服务推出的基金，投资者通过Taurus投资区块链数字资产演算服务领域，选择更多元，投资更简单，收益更长久稳定，风险更可控</div>
          <div className='desc'>此组合以投资实体计算机，并以参与区块链加密演算以及数据存储而获取相对应的奖励通证作为亮点，包含全球市场上市的及未上市的区块链服务。Taurus持有的各市场价值比重将由相关产品的风险类型(仅限于成长型与进取型）及HKGF的调仓策略共同决定及动态调整，且保持在25%及以下。 除BTC, ETH外，此组合还包含其他基于加密演算的组合，计算机分布分散。以长期持有、分散投资为理念，跟随全球区块链加密演算增长而一同获得收益。</div>
        </div>
      </div>
      <Footer></Footer>
    </div>
  );
}

export default App;
